import "./styles.css";
import React, { useState } from "react";
import Axios from "axios";
import Navbar from "./components/Navbar/Navbar";
import Movie from "./components/Movie/Movie";
import Frontui from "./components/Samples/Frontui";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import { GlobalContext } from "./context/globalContext";
import MovieDetail from "./components/MovieDetail/MovieDetail";

export default function App() {
  const [search, setSearch] = useState("");
  const [result, setResult] = useState([]);
  const [suggest, setSuggest] = useState([]);
  const [theme, setTheme] = useState(true);
  const [loader, setLoader] = useState(true);
  const [show, setShow] = useState(false);
  const [detail, setDetail] = useState({});

  const toggleMode = () => {
    setTheme(!theme);
  };

  const handleDetail = async (movieID) => {
    console.log(movieID);
    try {
      const result = await Axios.get(
        `http://www.omdbapi.com/?i=${movieID}&apikey=9d494647`,
        {
          header: {
            "Access-Control-Allow-Origin": "*"
          }
        }
      );
      console.log(result.data);
      setDetail(result.data);
      setLoader(false);
      setShow(false);
    } catch (e) {
      console.log(e);
    }
  };

  React.useEffect(() => {
    const onchangeItem = async () => {
      const res = await Axios.get(
        `http://www.omdbapi.com/?s=${search}&apikey=9d494647`
      );
      setSuggest(res.data.Search);
      setShow(true);
    };

    onchangeItem();
  }, [search]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const result = await Axios.get(
        `http://www.omdbapi.com/?s=${search}&apikey=9d494647`,
        {
          header: {
            "Access-Control-Allow-Origin": "*"
          }
        }
      );
      console.log(result.data.Search);
      setResult(result.data.Search);
      setLoader(false);
      setShow(false);
    } catch (e) {
      console.log(e);
    }
  };

  const inputChange = (e) => {
    setSearch(e.target.value);
  };

  return (
    <div className="App">
      <GlobalContext.Provider
        value={{
          theme,
          inputChange,
          handleSubmit,
          toggleMode,
          result,
          search,
          suggest,
          show,
          handleDetail,
          detail
        }}
      >
        <Router>
          <Navbar>
            {loader ? (
              <Route path="/" exact component={Frontui} />
            ) : (
              <Route path="/" exact component={Movie} />
            )}
            <Route path="/:id" component={MovieDetail} />
          </Navbar>
        </Router>
      </GlobalContext.Provider>
    </div>
  );
}
