import React from "react";
import { Container, Row, Col, Card } from "react-bootstrap";
import { GlobalContext } from "../../context/globalContext";

const MovieDetail = () => {
  let Global = React.useContext(GlobalContext);
  const { detail } = Global;
  return (
    <>
      <Container>
        <Row style={{ margin: "40px 90px", background: "black" }}>
          <Col xs={5} className="p-0">
            <Card style={{ border: "none", background: "black" }}>
              <Card.Img
                style={{ width: "300px" }}
                src={detail.Poster}
              ></Card.Img>
            </Card>
          </Col>
          <Col xs={7}>
            <h1 className="text-light">{detail.Title}</h1>
          </Col>
        </Row>
      </Container>
    </>
  );
};

export default MovieDetail;
